body, body * {
  margin: 0;
  padding: 0;
  font-family: 'Quark', sans-serif;
}

body {
  background: #F9F9F9;
}
.d-none {
  display: none;
}
.App{
  padding-top: 5.2em;
}

/* Header.jsx */

.header {
  background: #ffe7c1;
  background: -webkit-gradient(linear, left top, right top, color-stop(30%, #ffe7c1), color-stop(100%, #0572b8));
  background: -webkit-linear-gradient(left, #ffe7c1 30%, #0572b8 100%);
  background: -o-linear-gradient(left, #ffe7c1 30%, #0572b8 100%);
  background: -webkit-gradient(linear, left top, right top, color-stop(30%, #FFE7C1), to(#0572B8));
  background: -webkit-linear-gradient(left, #FFE7C1 30%, #0572B8 100%);
  background: -o-linear-gradient(left, #FFE7C1 30%, #0572B8 100%);
  background: linear-gradient(to right, #FFE7C1 30%, #0572B8 100%);
  min-height: 150px;
  /* box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.2); */
}
.icon {
  font-size: 20pt;
  font-weight: bold;
  line-height: 1em;
}

.header .container {
  padding: 1em;
  width: 100%;
  display: grid;
}
.title{
  padding-left: 1em;
  padding-right: 1em;
}

.header h1{
  font-size: 30pt;
}

.header h2{
  font-size: 20pt;
  font-weight: normal;
  line-height: 1em;
}

.inputgroup {
  -webkit-box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.3);
}

.searchbar {
  margin-bottom: .5em;
  margin-top: 1.5em;
  z-index: 10;
}

.searchbar input.search {
  font-size: 18pt;
  height: 40px;
  padding: .1em;
  padding-left: 1em;
  padding-right: 1em;
  border: 0;
  line-height: 1em;
  width: calc(100% - (.6em + 18pt));
  -webkit-transition: all .4s ease;
  -o-transition: all .4s ease;
  transition: all .4s ease;
}

.searchbar button {
  font-size: 18pt;
  padding-bottom: .2em;
  padding-top: .2em;
  padding-left: .3em;
  padding-right: .3em;
  color: #ededed;
  background: #444444;
  border: 0;
}
#check{
  margin-top: .5em;
  color: #ededed;
  font-size: 16pt;
  padding: .2em;
  float: left;
}
#check div{
  display: inline-block;
  padding-right: .5em;
}
#check input{
  width: 20px;
  height: 20px;
  line-height: 1em;
}
#check span{
  padding-right: .5em;
  padding-left: .2em;
}

@media (min-width: 768px) {
  .title {
    margin-top: .4em;
    max-width: 340px;
  }
  .header h2 {
    font-size: 28pt;
  }
  .header h1 {
    font-size: 50pt;
  }
  .header .container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
  .searchbar input.search {
    width: 300px;
  }
  .focus{
    width: 500px;
  }
}
@media (min-width: 990px) {
  .searchbar input.search.focus{
    width: 500px;
  }
  .App{
    padding-top: 3em;
  }
}
.header h1{
  font-size: 34pt;
}

.header h2{
  font-size: 24pt;
}

/* ItemList.jsx */

.itemhead {
  margin-top: .5em;
  text-align: center;
}

.itemhead h2 {
  font-size: 30pt;
}

.itemhead h3 {
  font-size: 16pt;
}

.cardarea {
  margin-bottom: 1em;
  margin-top: 1em;
}
.page{
  margin: 1em;
  margin-bottom: 2em;
  text-align: center;
}
.page-button {
  font-size: 14pt;
  font-weight: bold;
  padding: .4em;
  padding-top: .6em;
  line-height: 1em;
  background: #3993ce;
  color: #fff;
  border: 0;
  margin-left: .2em;
  margin-right: .2em;
  cursor: pointer;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
  -webkit-box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}
.page-button:hover{
  background: #0572b8;
}
.page-button.selected-button{
  -webkit-box-shadow: 0 0 5px 2px #3993ce;
          box-shadow: 0 0 5px 2px #3993ce;
  cursor: not-allowed;
}
.page-button.selected-button:hover{
  background: #3993ce;
}
.page-button.disabled-button{
  background: rgba(0, 0, 0, 0.3);
  cursor: not-allowed;
}
.page-button.numbers {
  font-size: 16pt;
  line-height: .6em;
}

/* Card.jsx */

.fish-card {
  height: 90%;
  background: #fff;
  margin-bottom: .5em;
  margin-top: .5em;
  -webkit-box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  -webkit-transition: all .7s ease;
  -o-transition: all .7s ease;
  transition: all .7s ease;
}
.fish-card:hover{
  background: #ededed;
  -webkit-box-shadow: 0 0 10px 3px #0572b8;
          box-shadow: 0 0 10px 3px #0572b8;
}

.fish-card img {
  padding: .7em;
  padding-top: .7em;
  padding-bottom: .2em;
  width: 100%;
  height: 150px;
  display: block;
  -o-object-fit: contain;
     object-fit: contain;
}

.fish-card span {
  display: block;
  font-size: 16pt;
  text-align: center;
  padding: .5em;
  padding-top: 0;
  padding-bottom: .7em;
}

/* Popup.jsx */
.fullscreen{
  width: 100vw;
  min-height: 100vh;
}
.popup-background{
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
}
#popup{
  position: fixed;
  z-index: 1000;
  overflow-y: auto;
}

/* PopupCard.jsx */
#p-close{
  position: absolute;
  top: -20px;
  right: -20px;
  width: 35px;
  height: 35px;
  background: #F5561D;
  color: #ededed;
  z-index: 1002;
  font-size: 1.6em;
  border-radius: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  cursor: pointer;
}
#p-card{
  z-index: 2000;
}
.p-card{
  position: absolute;
  background: #fff;
  top: 2em;
  left: 5%;
  width: 90%;
  /* min-height: 80%; */
  padding: 1em;
  margin-bottom: 2em;
}
@media (min-width: 1200px) {
  .p-card{
    left: 10%;
    width: 80%;
  }
}
.p-img{
  background: #222;
  height: 100%;
  padding: .5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.p-img img{
  max-height: 80vh;
  -o-object-fit: contain;
     object-fit: contain;
}
.p-info{
  padding: .5em;
  font-size: 18pt;
  line-height: 1.7em;
}
.p-title{
  font-weight: bold;
  /* padding-right: 2em; */
}



